@charset "UTF-8";
.animated-router {/** START: 自定义进出场动画 **/ /** END **/ }
.animated-router-container { /* 动画容器节点 */ }
.animated-router-in-transition {
    /* 页面动画中 */
    position: relative;
    width: 100%;
    overflow: hidden; 
}
.animated-router-forward-enter {
    -webkit-transform: translate(100%);
            transform: translate(100%); 
}
.animated-router-forward-enter-active {
    -webkit-transform: translate(0);
            transform: translate(0); 
}
.animated-router-forward-exit {
    -webkit-transform: translate(0);
            transform: translate(0); 
}
.animated-router-forward-exit-active {
    -webkit-transform: translate(-100%);
            transform: translate(-100%); 
}
.animated-router-backward-enter {
    -webkit-transform: translate(-100%);
            transform: translate(-100%); 
}
.animated-router-backward-enter-active {
    -webkit-transform: translate(0);
            transform: translate(0); 
}
.animated-router-backward-exit {
    -webkit-transform: translate(0);
            transform: translate(0); 
}
.animated-router-backward-exit-active {
    -webkit-transform: translate(100%);
            transform: translate(100%); 
}
.animated-router-forward-enter-active, .animated-router-forward-exit-active, .animated-router-backward-enter-active, .animated-router-backward-exit-active {
    /* 不同过渡阶段需要的过渡时间与缓动效果 */
    transition: -webkit-transform 0.3s linear;
    transition: transform 0.3s linear;
    transition: transform 0.3s linear, -webkit-transform 0.3s linear; 
}
.animated-router-forward-exit, .animated-router-backward-exit {
    position: absolute !important;
    width: 100%;
    top: 0;
    left: 0; 
}
.sku-list-item { height: 180px; }
.sku-list-image {  
    height: 180px;
    width: 180px;
    float: left;
}
.sku-list-info {
    height: 180px;
    float: left;
    margin-left: 182px;
    margin-top: -180px;
    padding: 30px 10px;
    color: #666666;
    font-size: 14px;
}
.sku-list-title {
    line-height: 24px;
}
/* 注意：不加这个样式，react-smooth-scrollbar将不会起作用 */
.home-container {
    height: 100%;
}

.carousel, .carousel-link, .carousel-img {
    height: 180px !important;
    width: 100%;
}
.carousel-link {
    display: inline-block;
}
.carousel-img {
    vertical-align: top;
}
/* 全局隐藏react-smooth-scrollbar的滚动条 */
.scrollbar-track {
  display:none !important;
}
/* 注意：不加这个样式，react-smooth-scrollbar将不会起作用 */
.top-home-scroll {
  height: 100%; 
}
.sku-page-wrapper, .sku-scrollbar-wrapper {
    position: fixed;
    height: 100%;
    width:100%;
    background-color: #f5f5f5;
}
.sku-main-img, .sku-main-img-container { height: 375px; }
.sku-top-bar {
    display: block;
    position: absolute;
    z-index: 999;
    width: 100%;
    background-color: white;
    opacity: 0.6;
}
.icon-back {
    margin: 20px;
    width: 12px;
    height: 12px;
    cursor: pointer;
    border-top: 1px solid #000;
    border-left: 1px solid #000;
    -webkit-transform: rotate(135deg);
    transform: rotate(315deg);
}

.sku-info { padding: 0 10px 10px 10px; background-color: #fff; }
.sku-info .price-wrapper span { color: #ff0036; }
.sku-info .price-wrapper .price { font-size: 24px; font-weight: 600; }
.sku-info .title { display: block; margin-top: 10px; }

.sku-spec { margin-top: 10px; padding: 10px 10px; background-color: #fff; }
.sku-spec .l { float: left; width: 35px; }
.sku-spec::before {
    float: right;
    margin-right: 10px;
    margin-top: 1px;
    content: " ";
    display: block;
    width: 12px;
    height: 12px;
    border-top: 1px solid #7d7d7d;
    border-left: 1px solid #7d7d7d;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
}
.sku-content {
    padding-bottom: 50px;
    margin-top: 10px;
}

.sku-detail-img { width:100%; }

.sku-bottom-bar {
    height: 50px;
    width: 100%;
    position: fixed;
    bottom: 0;
    background-color: #fff;
}
.sku-bottom-bar .icon-btn span { color: #aaa; font-size: 12px; }
.icon-btn {
    float: left;
    width: 15%;
    text-align: center;
}
.icon-shop {
    display: block;
    background: url(data:image/png;base64,) 50% no-repeat;
    background-size: auto 20px;
    width: 20px;
    height: 25px;
    margin: 2px auto;
}
.icon-srv {
    display: block;
    background: url(data:image/png;base64,) 50% no-repeat;
    background-size: auto 20px;
    width: 20px;
    height: 25px;
    margin: 2px auto;
}
.icon-cart {
    float: left;
    line-height: 50px;
    width: 35%;
    background-color: #ff9600;
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
}
.icon-buy {
    float: left;
    width: 35%;
    line-height: 50px;
    background-color: #e4393c;
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
}
body { 
    background-color: #fff; 
    margin:0; padding: 0;
    height:100%;
}
*, html, body, p, div, li, span, label, section, a, i {
    font-family: -apple-system,Helvetica,sans-serif;
    font-size: 14px;
    color: #051b28;
    line-height: 1.5;
}
/* 使首页TabBar全屏显示 */
.router-container, #root, .cache-route-wrapper {
    position: fixed;
    width: 100%;
    height: 100%;
}

span.light { color: #999; }
span.small { font-size: 12px; }
span.red { color: #e4393c; }
span.price { color:#ff0036; }
